:root {
    --background_page: #fff;
    --header_background: #AC0FBE;
}

.Header
{  
    position: relative;
    background-color:#AC0FBE;
    border-bottom-left-radius: 50px;
    padding-bottom: 15px;
    z-index: 100;
}

.CatItem
{
    width:110px;
    height: 130px;
    background: #fff;
    border-radius: 22px;
    margin-left:15px;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, .25);
}

.InfoHeader
{
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:#fff;
    margin-bottom: 40px;
}

.InfoUser
{
    float:left;
    margin-left: 5px;
}
.InfoNotice
{
    float: right;
}

.CatItemText
{
    font-weight: 600;
    font-size: 14px;
}

.CatItemImg
{
    width: 61px;
    height: 61px;

    margin-top: 15px;
    margin-bottom: 30px;
    border-radius: 50px;
}

.HeaderImg
{
    width: 40px;
    height: 40px;

    border-radius: 50px;
}

.InfoImg
{
    margin-left: 10px;
    float:right;
}

.TextBold
{
    font-weight: 600;
    font-size: 16px;
}

.TextSmall
{
    font-size: 13px;
}

.ActionHeader
{
    display: flex;
    align-items: flex-end;

    padding-left: 20px;
    position: relative;
    background: #eeeeee;
    border-bottom-left-radius: 50px;
    margin-top: -50px;
    height: 75px;
    z-index: 99;
}

.ActionItem
{
    padding: 5px;
    background: #AE97D6;
    color: #fff;
    text-align: center;
    border-radius: 50px;
    padding-left: 15px;
    padding-right: 15px;

    margin-left: 10px;

    font-size:14px;
}
.Persik {
  display: block;
  width: 30%;
  max-width: 240px;
  margin: 20px auto;
}

